  .link::after {
    content: "";
    display: inline-block;
    height: 1px;
    width: 0;
    opacity: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    transition: width .3s, opacity .15s;
    background: #8c8f94; }
  .link:hover::after {
    width: 100%;
    opacity: 1; }
  .link:visited {
    color: #72757b; }

