  .sub-title + h3 {
    margin-top: 0; }
  .sub-title--separator-top, .sub-title--separator-bottom {
    position: relative; }
    .sub-title--separator-top:before, .sub-title--separator-bottom:before {
      content: "";
      display: block;
      height: 2px;
