/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slick-slide:focus,
.slick-arrow:focus {
  outline: none; }

.slick-arrows {
  left: 0;
  right: 0;
  position: relative;
  z-index: 1; }

.slick-arrow {
  display: block;
  width: 22px;
  height: 41px;
  border: 0;
  position: absolute;
  cursor: pointer;
  opacity: .2;
  transition: .2s; }
  .slick-arrow:hover, .slick-arrow:focus, .slick-arrow:active {
    opacity: .5; }

.slick-prev,
.slick-next {
  background: url("../img/slider-arrow.png");
  background-size: contain;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%); }

.slick-prev {
  left: 0; }

.slick-next {
  transform: translateY(-50%) rotate(180deg);
  right: 0; }

.slick-dots {
  margin: 0;
  padding: 0;
