.star-array {
    white-space: nowrap;
    list-style: none;
    padding: 0;
}

.star {
    width: 24px;
    height: 24px;
    display: inline-block;
    vertical-align: middle;
    background-image: url('data:image/png;base64,');
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 1px;

    &-empty {
    	background-image: url('data:image/png;base64,');
        background-repeat: no-repeat;
    }
}